* {
    box-sizing: border-box;
}

.App {
    margin: 0 auto;
    box-sizing: border-box;
    background-color: #284652;
    height: 100vh;
    padding-top: 1%;
}

.error {
    color: orangered;
}

.container {
    max-width: 330px;
    width: 100%;
    margin: 0 auto;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #DBE9EE;
    border: 4px solid #93ACCA;
    border-radius: 10px;
}

.counter {
    width: 100%;
    height: auto;
    background-color: #DBE9EE;
    border: 4px solid #C0D6DF;
    border-radius: 10px;
    margin: 10px 0;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.inner {
    max-width: 300px;
    margin: 0 auto;
}

.setting-container {
    width: 100%;
    /*height: auto;*/
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-color: #DBE9EE;
    border: 4px solid #C0D6DF;
    border-radius: 10px;
    margin: 10px 0;
}



.wrapper {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.btn-wrapper{
    width: 85%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    border-radius: 5px;
}

.set-btn-wrapper button {
    /*margin: 0;*/
}

.counterContainer {
    display: flex;
    justify-content: center;
    align-items: center;
}

.sectionName {
    background-color: #C0D6DF;
    padding: 5px 10px;
    font-size: 22px;
    border-radius: 0 0 8px 8px;
    color: #394b56;
    margin-bottom: 15px;
    text-align: center;
}




